import React, {useEffect, useRef} from 'react'

export const HorizontalScroll = ({children, className}) => {
    const wrapper = useRef(null);

    const handleIn = (e) => {
        console.log('in')
        console.log(e.target);
    }

    const handleScroll = (e) => {
        const window = e.currentTarget;
        const windowBottomPosition = Math.round(window.scrollY + window.innerHeight)

        if(wrapper.current.getBoundingClientRect().top <= 0) {
            console.log('true')
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)

        return () => {
            window.removeEventListener('scroll', handleScroll)
        }
    }, [])

    return (
        <div ref={wrapper} onScroll={handleScroll} className={`horizontal-scroll ${className}`}>
            {children}
        </div>
    )
}